import { Component, OnInit } from "@angular/core";
import { FormGroup, FormControl, Validators } from "@angular/forms";
import { AuthService } from "../services/auth.service";
import { ActivatedRoute, Router } from "@angular/router";
import swal from 'sweetalert2'

@Component({
    selector: 'log-in',
    templateUrl: 'login.component.html',
    styleUrls: ['login.component.css']
})
export class LoginComponent implements OnInit {
    loginForm: FormGroup;
    isLoading = false;

    constructor(private auth: AuthService, private route: ActivatedRoute, private router: Router) { }
    ngOnInit() {
        this.auth.logout();
        this.loginForm = new FormGroup({
            'phone': new FormControl(null, Validators.required),
            'password': new FormControl(null, [Validators.required, Validators.minLength(4)]),
        });
    }

    onSubmit() {
        this.isLoading = true;
        let phone = this.loginForm.controls['phone'].value;
        let pass = this.loginForm.controls['password'].value;
        console.log("em=>", phone, " , pass=>", pass);

        this.auth.signin(phone, pass).subscribe(
            (user) => {
                this.isLoading = false;
                this.auth.setUser(user);
                console.log("user logged!");
                let returnUrl = this.route.snapshot.queryParams['url']; /*if user is redirected by the authGuard */
                if (returnUrl) {
                    this.router.navigate([returnUrl]);
                }
                else {
                    this.router.navigate(['/']);
                }
            },
            (error) => {
                console.log("url=>>>",this.route.snapshot.queryParams['url'])
                this.isLoading = false;
                swal("خطأ", "من فضلك ادخل بيانات صحيحة ", "error");
            }
        );
    }
}


