import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { Router, Routes, RouterModule } from "@angular/router";
import * as moment from 'moment';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { AuthService } from '../services/auth.service';
import { UserService } from '../services/user-service.service';
import swal from 'sweetalert2';
@Component({
    selector: 'add-employee',
    templateUrl: './add-employee.component.html',
    styleUrls: ['./add-employee.component.css']
})

export class AddEmpolyeeComponent implements OnInit {
    employeeForm: FormGroup;
    @ViewChild('img') inputFile: ElementRef;
    currentImg = 'assets/img/find_user.png';
    loading: boolean = false;
    constructor(
        private userService: UserService,
        private router: Router,
    ) { }
    ngOnInit() {
        this.initForm();
    }

    onSubmit() {
        let empolyee = new FormData();
        if (this.currentImg != 'assets/img/find_user.png') {
            empolyee.append("img", this.inputFile.nativeElement.files[0]);
        }
        empolyee.append('name', this.employeeForm.controls['name'].value);
        empolyee.append('phone', this.employeeForm.controls['phone'].value);
        empolyee.append('nationaId', this.employeeForm.controls['nationaId'].value);
        empolyee.append('role', this.employeeForm.controls['role'].value);
        empolyee.append('salary', this.employeeForm.controls['salary'].value);

        console.log("form", this.employeeForm.value);
        this.loading = true;
        this.userService.addEmployeee(empolyee).subscribe(
            (user) => {
                this.loading = false;
                swal("نجاح", "تم اضافة " + user['name'], "success");
                this.router.navigate(['all-employees']);
            },
            (error) => {
                swal("خطأ", "تأكد من ادخال بيانات صحية ثم عاود المحاولة", "error");
                this.loading = false;
                console.log(error)
            }
        );
    }
    onImageInputChange(input) {
        let self = this;
        if (input.files && input.files[0]) {
            let reader = new FileReader();
            reader.onload = function (e) {
                self.currentImg = e.target['result'];
            };
            reader.readAsDataURL(input.files[0]);
        }
    }

    initForm() {
        this.employeeForm = new FormGroup({
            'name': new FormControl(null, Validators.required),
            'phone': new FormControl(null, [Validators.required, Validators.maxLength(12), Validators.pattern(/^\d{10,12}$/)]),
            'nationaId': new FormControl(null, [Validators.required, Validators.maxLength(14), Validators.minLength(14)]),
            'role': new FormControl(null),
            'salary': new FormControl(null),
        });
    }
}


