import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from '../../../environments/environment';
import { Router } from '@angular/router';
import { User } from '../models/user.model';

@Injectable()
export class AuthService {
    currentUser;
    constructor(private http: HttpClient, private router: Router) { }
    signin(phone, password) {

        console.log("   signin-url=>", environment.apiPath + 'signin')
        let body = { "phone": phone, "password": password };
        console.log("From Service-body=>", body)
        return this.http.post(environment.apiPath + 'signin', body);
    }
    signup(user: FormData) {
        return this.http.post<User>(environment.apiPath + 'signup', user, { observe: "response" });
    }
    setUser(user) {
        this.currentUser = user;
        console.log("Current user =>", this.currentUser);
        localStorage.setItem('leSalonUser', JSON.stringify(this.currentUser));
    }
    isAuthenticated(): boolean {
        if (this.checkUser() == false) { return false }
        else { return true; }
    }
    checkUser() {
        let user = localStorage.getItem('leSalonUser');
        if (user) {
            this.currentUser = JSON.parse(user);
        }
        else { return false; }
    }

    getToken() {
        return 'Bearer ' + this.currentUser.token;
    }
    logout() {
        this.currentUser = null;
        localStorage.removeItem('leSalonUser');
        this.router.navigate(['login']);
    }

}



