import { Component, OnInit } from '@angular/core';
import { Router, Routes, RouterModule } from "@angular/router";
import * as moment from 'moment';
@Component({
  selector: 'app-logInSignUp-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})

export class logInSignUpHeaderComponent implements OnInit {

  logInSignUpBtn: string;
  logInSignUpPath: string;
  todayDate
  constructor(private router: Router) { }

  ngOnInit() {
    this.todayDate = moment(new Date())
    if (this.router.url == '/login') {
      this.logInSignUpBtn = "إنشاء حساب";
      this.logInSignUpPath = "/signup";
    }
    else if (this.router.url == '/signup') {
      this.logInSignUpBtn = "تسجيل دخول";
      this.logInSignUpPath = "/login";
    }
  }

}





