/*Outer Imports*/
import { NgModule } from '@angular/core';
import { Routes, Router, RouterModule } from '@angular/router';

/*Our Components*/
// import { SignupComponent } from './signup/signup.component';
// import { HomeComponent } from './home/home.component';
// import { LoginComponent } from './login/login.component';
// import { ErrorComponent } from '../shared/404-error-component/error-component.component';
// import { ProfileComponent } from './profile/profile.component';

/*Our Services*/
import { AuthGuard } from './services/auth.gaurd.service';
import { LoginComponent } from './login/login.component';
import { AddEmpolyeeComponent } from './add-employee/add-employee.component';
import { HomeComponent } from './home/home.component';
import { IndexComponent } from './index/index.component';
import { Services_PackagesComponent } from './services&packages/services&packages.component';
import { ClientsComponent } from './clients/clients.component';
import { AllEmpolyeesComponent } from './all-empolyees/all-empolyees.component';
import { BillComponent } from './bill/bill.component';
import { AllBillsComponent } from './all-bills/all-bills.component';


const ROUTES: Routes = [
    { path: 'login', component: LoginComponent },
    {
        path: '', component: HomeComponent, canActivate: [AuthGuard], children: [
            { path: '', component: IndexComponent },
            { path: 'service-packages', component: Services_PackagesComponent },
            { path: 'clients', component: ClientsComponent },
            { path: 'add-new-empolyee', component: AddEmpolyeeComponent },
            { path: 'all-employees', component: AllEmpolyeesComponent },
            { path: 'appointments/:id/clients/:clId/service-bill', component: BillComponent },
            { path: 'all-bills', component: AllBillsComponent },

        ]
    },

    { path: 'add-employee', component: AddEmpolyeeComponent },

];



@NgModule({
    imports: [
        RouterModule.forRoot(ROUTES)
    ],
    exports: [
        RouterModule
    ]
})
export class AppRoutingModule {
}



