import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from '../../../environments/environment';
import { Router } from '@angular/router';

@Injectable()
export class UserService {
    constructor(private http: HttpClient) { }

    addService(name: string, nameEn: string, price: number, description?: string) {
        let body = {
            "name": name,
            "nameEn": nameEn,
            "price": price
        }
        return this.http.post(environment.apiPath + 'services', body);
    }
    fetchServices() {
        return this.http.get<Array<any>>(environment.apiPath + 'services');
    }
    addPackage(body) {
        return this.http.post(environment.apiPath + 'bakats', body);
    }
    fetchPackages() {
        return this.http.get<Array<any>>(environment.apiPath + 'bakats');
    }
    fetchClients() {
        return this.http.get<Array<any>>(environment.apiPath + 'clients');
    }
    fetchClientById(clientId) {
        return this.http.get<any>(environment.apiPath + `clients/${clientId}`);
    }
    addClient(name: string, phone: number) {
        let body = {
            "name": name,
            "phone": phone,
        }
        return this.http.post(environment.apiPath + 'clients', body);
    }
    addAppiontment(clientId, date) {
        let body = {
            "client": clientId,
            "Date": date,
        }
        return this.http.post(environment.apiPath + 'appointments', body);
    }
    fetchAppiontments() {
        console.log("fetchAppiontments_url===>", environment.apiPath + 'appointments')
        return this.http.get<Array<any>>(environment.apiPath + 'appointments');
    }
    fetchEmployeees() {
        return this.http.get<Array<any>>(environment.apiPath + 'employees');
    }
    addEmployeee(employee: FormData) {
        return this.http.post(environment.apiPath + 'employees', employee);
    }
    addServiceBill(body) {
        return this.http.post(environment.apiPath + 'payments', body);
    }
    fetchAllBills() {
        return this.http.get<Array<any>>(environment.apiPath + 'payments');
    }
}


