import { Component, OnInit } from "@angular/core";
import { UserService } from "../services/user-service.service";
import swal from "sweetalert2";
import { ActivatedRoute, Router } from "@angular/router";
import { CompleterService, CompleterData, CompleterItem } from 'ng2-completer';

declare var $
@Component({
    selector: 'bill',
    templateUrl: 'bill.component.html',
    styleUrls: ['bill.component.css']
})
export class BillComponent implements OnInit {

    totalPriceAfterDiscount: number = 0;
    allServices: any[] = [];
    allBarbers: any[] = [];
    allBarbersNames: any[] = [];
    selectedServices: any[] = [];
    totalPrice: number = 0;
    loadingServices: boolean = false;

    currentAppointmentId;
    currentClient;

    selectedBarber: { name: string, id: string } = { name: "", id: "" };

    discountVal = 0;
    discountFlag = false;
    barberSelseted = false;

    constructor(
        private userService: UserService,
        private router: Router,
        private route: ActivatedRoute,
    ) { }
    ngOnInit() {
        this.route.params.subscribe(
            (routParams) => {
                this.currentAppointmentId = routParams['id'];
                this.userService.fetchClientById(routParams['clId']).subscribe(
                    (client) => {
                        this.currentClient = client;
                        console.log("currentClient=>", this.currentClient);
                    }
                );
            }
        )
        this.userService.fetchEmployeees().subscribe(
            (all) => {
                this.allBarbers = all;
                this.allBarbers.forEach(
                    (bar) => {
                        this.allBarbersNames.push(bar.name);
                    }
                )
            });
        this.fetchServices();
    }
    fetchServices() {

        this.loadingServices = true;
        this.userService.fetchServices().subscribe(
            (response) => {
                this.loadingServices = false;
                this.allServices = response;
                console.log("All Service =>", response)
            },
            (error) => {
                this.loadingServices = false;
                if (error.status == 0) {
                    swal("خطأ", "تأكد من الاتصال بالانترنت ثم عاود المحاولة", "error")
                }
            }
        );
    }
    onShowBill() {
        $('#modal').modal('dismiss');
        console.log("onShowBill()")
    }
    protected onSelected(item: CompleterItem) {
        this.barberSelseted = true;
        this.selectedBarber.name = item ? item.title : "";
        this.allBarbers.forEach(
            (bar) => {
                if (bar.name === this.selectedBarber.name) {

                    this.selectedBarber = bar
                 }
            }
        );

    }
    onSelectService(ser) {
        if (this.selectedServices.includes(ser)) {
            this.selectedServices.splice(this.selectedServices.indexOf(ser), 1);
        }
        else {
            this.selectedServices.push(ser);
        }
        console.log("selsected Services =>", this.selectedServices)
        this.calculateTotalPrice();
    }
    calculateTotalPrice() {
        this.totalPrice = 0;
        for (let i = 0; i < this.selectedServices.length; i++) {
            this.totalPrice += this.selectedServices[i]['price'];
        }
        this.totalPriceAfterDiscount = this.totalPrice - ((this.discountVal / 100) * this.totalPrice);
        return this.totalPrice;
    }
    calculateDiscount() {
        this.discountFlag = true;
    }

    onBillSubmition() {
        if (!this.barberSelseted) {
            swal("خطأ", "لابد من اختيار الحلاق", "error");
            return;
        }
        let services: any[] = [];
        this.selectedServices.forEach((service) => {
            services.push(service.id);
        })
        let body = {
            appointment: this.currentAppointmentId,
            client: this.currentClient.id,
            employee: this.selectedBarber.id,
            services: services,
            discount: this.discountVal,
            actualTotalPrice: this.totalPrice,
            finalTotalPrice: this.totalPriceAfterDiscount,
        }
        console.log("bill-body=>", body)
        this.userService.addServiceBill(body).subscribe(
            (res) => {
                swal("نجاح", "تم اضافة الفاتورة", "success");
                console.log("bill-res=>", res)
                // this.router.navigate(['']);
            }, (error) => {
                swal("خطأ", "حدث خطأ..يرجى المحاولة مرة أخرى", "error");
            }
        );
    }
}


