import { Component, OnInit } from "@angular/core";
import { UserService } from "../services/user-service.service";

@Component({
    selector: 'all-bills',
    templateUrl: 'all-bills.component.html',
    styleUrls: ['all-bills.component.css']
})
export class AllBillsComponent implements OnInit {
    allBills: any[] = [];
    constructor(
        private userService: UserService
    ) { }
    ngOnInit() {
        this.userService.fetchAllBills().subscribe(
            (response) => {
                this.allBills = response;
                console.log("all Bills =>", this.allBills)
            }
        )
    }
}


