import { Component, OnInit } from "@angular/core";
import { FormGroup, FormControl, Validators } from "@angular/forms";
import { UserService } from "../services/user-service.service";
import swal from "sweetalert2";

@Component({
    selector: 'all-clients',
    templateUrl: 'clients.component.html',
    styleUrls: ['clients.component.css']
})
export class ClientsComponent implements OnInit {
    allClients: any[] = [];
    clientForm: FormGroup;

    constructor(
        private userService: UserService,

    ) { }
    ngOnInit() {
        this.clientForm = new FormGroup({
            'name': new FormControl(null, Validators.required),
            'phone': new FormControl(null, Validators.required)
        });
        this.userService.fetchClients().subscribe(
            (response) => {
                this.allClients = response;
            }
        );
    }
    onClientSubmit() {
        // console.log("client To Add =>", this.clientForm.value);
        this.userService.addClient(this.clientForm.get("name").value, this.clientForm.get("phone").value).subscribe(
            (response) => {
                swal("نجاح", "تم اضافة العميل", "success");
                this.allClients = [response, ...this.allClients];

                this.clientForm.reset();
            }
        );

    }
}


