import { Component, OnInit } from "@angular/core";
import { UserService } from "../services/user-service.service";
import { FormGroup, FormControl, Validators } from "@angular/forms";
import swal from "sweetalert2";

@Component({
    selector: 'services&packages',
    templateUrl: 'services&packages.component.html',
    styleUrls: ['services&packages.component.css']
})
export class Services_PackagesComponent implements OnInit {
    allServices: any[] = [];
    allPackages: any[] = [];
    serviceForm: FormGroup;
    packageForm: FormGroup;

    constructor(private userService: UserService) { }

    ngOnInit() {
        this.userService.fetchServices().subscribe(
            (response) => {
                this.allServices = response.reverse();
                console.log("All Service =>", response)
            }
        );
        this.userService.fetchPackages().subscribe(
            (response) => {
                this.allPackages = response.reverse();
                console.log("All Packages =>", response)
            }
        );
        this.serviceForm = new FormGroup({
            'nameAr': new FormControl(null, Validators.required),
            'nameEn': new FormControl(null, Validators.required),
            'price': new FormControl(null, Validators.required),
            'description': new FormControl(null, Validators.required)
        });
        this.packageForm = new FormGroup({
            'name': new FormControl(null, Validators.required),
            'price': new FormControl(null, Validators.required),
            'description': new FormControl(null, Validators.required)
        });
    }
    onServiceSubmit() {
        console.log("serFormSubmition=>", this.serviceForm.value)
        this.userService.addService(
            this.serviceForm.get('nameAr').value,
            this.serviceForm.get('nameEn').value,
            this.serviceForm.get('price').value,
            this.serviceForm.get('description').value,
        ).subscribe(
            (response) => {
                this.allServices = [response, ...this.allServices];
                swal("نجاح", "تم اضافة الخدمة", "success")
                console.log("Service is added=>", response)
            }
        );
    }
    onPackageSubmit() {
        console.log("packageForm=>", this.packageForm.value)
        this.userService.addPackage(this.packageForm.value).subscribe(
            (response) => {
                this.allPackages = [response, ...this.allPackages];
                swal("نجاح", "تم اضافة الباقة", "success")
                console.log("Package is added=>", response)
            },(error)=>{
                swal("خطأ", "حدث خطأ..حاول مرة أخرى!", "error")
                console.log("error=>", error)

            }
        );
    }
}








