/*Outer Imports*/
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { AppRoutingModule } from './app.routing.module';
import { AuthService } from './services/auth.service';
import { AuthGuard } from './services/auth.gaurd.service';
import { AuthInterceptor } from './services/auth.interceptor.service';
import { LoginComponent } from './login/login.component';
import { EmployeeService } from './services/employee-service.service';
import { AddEmpolyeeComponent } from './add-employee/add-employee.component';
import { HomeComponent } from './home/home.component';
import { logInSignUpHeaderComponent } from './header/header.component';
import { IndexComponent } from './index/index.component';
import { UserService } from './services/user-service.service';
import { AngularDateTimePickerModule } from 'angular2-datetimepicker';
import { Services_PackagesComponent } from './services&packages/services&packages.component';
import { ClientsComponent } from './clients/clients.component';
import { AllEmpolyeesComponent } from './all-empolyees/all-empolyees.component';
import { BillComponent } from './bill/bill.component';
import { Ng2CompleterModule } from "ng2-completer";
import { AllBillsComponent } from './all-bills/all-bills.component';


@NgModule({
  declarations: [
    LoginComponent,
    AddEmpolyeeComponent,
    AllEmpolyeesComponent,
    HomeComponent,
    logInSignUpHeaderComponent,
    IndexComponent,
    Services_PackagesComponent,
    ClientsComponent,
    BillComponent,
    AllBillsComponent
  ],
  imports: [
    AppRoutingModule,
    FormsModule,
    HttpClientModule,
    ReactiveFormsModule,
    CommonModule,  
    AngularDateTimePickerModule,
    Ng2CompleterModule
   ],
  exports: [
    AppRoutingModule,

  ],
  providers: [
    AuthService,
    AuthGuard,
    EmployeeService,
    UserService,
    {
      provide: HTTP_INTERCEPTORS,
      useClass: AuthInterceptor,
      multi: true,
    },




  ]
})

export class CoreModule { }



