import { Component, OnInit } from "@angular/core";
import { UserService } from "../services/user-service.service";
import { FormGroup, FormControl, Validators } from "@angular/forms";
import swal from "sweetalert2";
import *as moment from "moment";
import { Router } from "@angular/router";
import { CompleterService, CompleterData, CompleterItem } from 'ng2-completer';
import { Observable } from 'rxjs/Observable';
import 'rxjs/Rx';
declare var $
@Component({
    selector: 'index',
    templateUrl: 'index.component.html',
    styleUrls: ['index.component.css']
})
export class IndexComponent implements OnInit {

    allClients: any[] = [];
    allClientsNames: any[] = [];

      protected selectedColor: string;




    phone = 10354646543;
    allServices: any[] = [];
    allAppiontments: any[] = [];

    date: number = 0;
    loadingServices: boolean = false;
    appointmentsLoading: boolean = false;
    settings = {
        bigBanner: true,
        timePicker: true,
        format: 'dd-MMM-yyyy hh:mm a',
        defaultOpen: false,
    }
    clientForm: FormGroup;
    appiontmentForm: FormGroup;
    selectedClientId: any;
    constructor(
        private userService: UserService,
        private router: Router,
    ) { }
    ngOnInit() {
        this.date = moment(new Date().getTime()).valueOf()
        this.clientForm = new FormGroup({
            'name': new FormControl(null, Validators.required),
            'phone': new FormControl(null, Validators.required)
        })
        this.appiontmentForm = new FormGroup({
            'clientPhone': new FormControl(null, Validators.required),
            'date': new FormControl(null, Validators.required)
        })

        this.loadingServices = true;
        this.userService.fetchServices().subscribe(
            (response) => {
                this.loadingServices = false;
                this.allServices = response;
                console.log("All Service =>", response)
            }
        );
        this.appointmentsLoading=true;
        this.userService.fetchAppiontments().subscribe(
            (response) => {
                this.appointmentsLoading=false;
                this.allAppiontments = response.reverse();
                console.log("All Appiontments =>", response);
            }
        );
        this.userService.fetchClients().subscribe(
            (response) => {
                this.allClients = response;
                console.log("allClients", this.allClients);
                this.allClients.forEach(
                    (client) => { this.allClientsNames.push(client.name) }
                );
                console.log("allClientsNames", this.allClientsNames);

            }
        );
    }
    protected onSelected(item: CompleterItem) {
        this.selectedColor = item ? item.title : "";
        console.log("selectedColor", this.selectedColor);
        this.allClients.forEach(
            (client) => {
                if (client.name === this.selectedColor) {
                    this.selectedClientId = client.id
                }
            }
        );

    }
    onDate() {
        console.log("All Service =>")
    }
    onClientSubmit() {
        // console.log("client To Add =>", this.clientForm.value);
        this.userService.addClient(this.clientForm.get("name").value, this.clientForm.get("phone").value).subscribe(
            (response) => {
                swal("نجاح", "تم اضافة العميل", "success");
                this.clientForm.reset();
            }
        );
    }
    onAppointmentSubmit() {
        this.date = moment(new Date(this.date).getTime()).valueOf();
        this.userService.addAppiontment(this.selectedClientId, this.date).subscribe(
            (response) => {
                swal("نجاح", "تم اضافة الموعد", "success");
                this.appiontmentForm.reset();
                this.appointmentsLoading=true;
                this.userService.fetchAppiontments().subscribe(
                    (response) => {
                        this.appointmentsLoading=false;
                        this.allAppiontments = response.reverse();
                        console.log("All Appiontments =>", response);
                    }
                );
                console.log("added Appointment =>", response)
            }
        );
    }
    onCalculateBill(appointment) {
        this.router.navigate(['appointments', appointment.id,'clients',appointment.client.id, 'service-bill']);
    }

}


