import { Component, OnInit } from "@angular/core";
import { UserService } from "../services/user-service.service";

@Component({
    selector: 'all-empolyees',
    templateUrl: 'all-empolyees.component.html',
    styleUrls: ['all-empolyees.component.css']
})
export class AllEmpolyeesComponent implements OnInit {
    allEmpolyees: any[] = [];
    nameArr: string[] = [];
    constructor(
        private userService: UserService
    ) { }
    ngOnInit() {
        this.userService.fetchEmployeees().subscribe(
            (response) => {
                this.allEmpolyees = response;
                console.log("all empolyees=>", this.allEmpolyees);
            }
        );
    }
    splitName(name) {
        this.nameArr = name.split(" ");
        return this.nameArr[0];
    }
}


